import trailerStars from "../../components/trailerStars.vue"
import lineWapper from "../../components/line.vue"
export default {
	data() {
		return {
			trailerInfo: {},
			plotPicesArray: [], //剧照
			directerArray: [], //导演列表
			actorArray: [] //演员列表
		}
	},
	onLoad(params) {
		// 获取上一个页面传入的参数 
		var trailerId = params.trailerId;
		// 获取预告片的详细信息
		var that = this;
			// 通过api设置导航栏的属性
		uni.setNavigationBarColor({
			frontColor:"#ffffff",
			backgroundColor:"#000000"
		}),
		uni.showLoading({
			mask: true,
			title: '请稍后'
		});
		uni.showNavigationBarLoading();
		var serverUrl = that.serverUrl;
		// 查询猜你喜欢
		uni.request({
			url: serverUrl + '/search/trailer/' + trailerId, //直接在url侯曼拼接也可以
			method: "POST",
			header: {
				'Content-Type': 'application/x-www-form-urlencoded'
			},

			data: {
				qq: ''
			},
			success: (res) => {
				// debugger
				console.log(res.data);
				if (res.data.status == 200) {
					var trailerInfo = res.data.data;
					that.trailerInfo = trailerInfo;
					// console.log(JSON.parse(trailerInfo.plotPics))
					// 把剧照的字符串转化为json array
					that.plotPicesArray = JSON.parse(trailerInfo.plotPics);
				}
			},
			// 请就到数据  隐藏等待图标
			complete: () => {
				uni.hideNavigationBarLoading()
				uni.hideLoading()
			}
		});
		// 获取导演
		uni.request({
			url: serverUrl + '/search/staff/' + trailerId + '/1', //直接在url侯曼拼接也可以
			method: "POST",
			header: {
				'Content-Type': 'application/x-www-form-urlencoded'
			},

			data: {
				qq: ''
			},
			success: (res) => {
				// debugger
				console.log(res.data);
				if (res.data.status == 200) {
					that.directerArray = res.data.data;
					// debugger
				}
			},
		});
		// 获取演员
		uni.request({
			url: serverUrl + '/search/staff/' + trailerId + '/2', //直接在url侯曼拼接也可以
			method: "POST",
			header: {
				'Content-Type': 'application/x-www-form-urlencoded'
			},
			data: {
				qq: ''
			},
			success: (res) => {
				// debugger
				console.log(res.data);
				if (res.data.status == 200) {
					that.actorArray = res.data.data;
					// debugger
				}
			},
		});
		
	
	},
	methods: {
	lookMe(e){
		var that = this;
		var imgIndex = e.currentTarget.dataset.imgindex;
		uni.previewImage({
			urls:that.plotPicesArray,
			current:that.plotPicesArray[imgIndex]
			
		})
	},
	},
	// 注册自定义组件
	components: {
		trailerStars,
		lineWapper
	},
}
